<template>
    <div class="page-view full_page">
        <div class="page_breadcrumb">
            <Breadcrumb>
                <BreadcrumbItem to="/"></BreadcrumbItem>
                <BreadcrumbItem></BreadcrumbItem>
            </Breadcrumb>
        </div>
        <div class="page_content">
            <div style="width: 800px;margin:auto;padding-top:50px;">
                <Steps :current="stepIndex">
                    <Step title=""></Step>
                    <Step title=""></Step>
                    <Step title=""></Step>
                </Steps>
            </div>
            <div style="min-height:500px;width:500px;margin:auto;padding-top:50px;">
                <div v-show = "stepIndex === 0">
                    <Form ref="formValidate" :label-width="100">
                        <FormItem label="" prop="city">
                            <Select placeholder="">
                                <Option value="ant-design@alipay.com">ant-design@alipay.com</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="" prop="name">
                            <Input placeholder="">
                                <Select v-model="select1" slot="prepend" style="width: 80px">
                                    <Option value="http"></Option>
                                    <Option value="https"></Option>
                                </Select>
                            </Input>
                        </FormItem>
                        <FormItem label="" prop="name">
                            <Input placeholder=""></Input>
                        </FormItem>
                        
                        <FormItem>
                            <Button type="primary" @click="handleSubmit(1)"></Button>
                        </FormItem>
                    </Form>
                </div>
                <div v-show = "stepIndex === 1">
                    <Form ref="formValidate" :label-width="100">
                        <FormItem label="">
                            ant-design@alipay.com
                        </FormItem>
                        <FormItem label="">
                            test@example.com
                        </FormItem>
                        <FormItem label="">
                            Alex
                        </FormItem>
                        <FormItem label="">
                            500
                        </FormItem>
                        <FormItem label="" prop="password">
                            <Input placeholder="" style="width:200px;"></Input>
                        </FormItem>
                        <FormItem>
                            <Button type="primary"  @click="handleSubmit(2)"></Button>
                            <Button type="ghost" @click="handleSubmit(0)" style="margin-left: 10px"></Button>
                        </FormItem>
                    </Form>
                </div>
                <div v-show = "stepIndex === 2">
                    <div slot="body">
                        <div class="result" style="padding-top: 48px; margin-bottom: 100px;text-align:center;">
                            <Icon type="checkmark-circled" :size="80" color="#52c41a"></Icon>
                            <div class="title34" style="font-size:32px;margin-top:20px;"></div>
                            <div style="font-size: 14px;line-height: 22px;color: rgba(0, 0, 0, 0.45);margin-bottom: 24px;"> 
                                
                            </div>
                            <div style="background: #fafafa;padding: 24px 40px;border-radius: 2px;text-align: left;">
                                <p>ant-design@alipay.com</p>
                                <p>test@example.com</p>
                                <p>Alex</p>
                                <p>500 </p>
                            </div>
                            <div style="margin-top:30px;">
                                <Button type="primary" @click="handleSubmit(0)"></Button>
                                <Button type="ghost" style="margin-left: 8px"></Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                stepIndex: 0,
                select1: ''
            }
        },
        methods: {
            handleSubmit (val) {
                this.stepIndex = val
            },
            handleReset (name) {
                this.$refs[name].resetFields()
            }
        }
    }
</script>



